import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  DatePicker,
  Input,
  message
} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect, CsxSearch, CsxMultipleSearch} from 'Form'
import {API_PRODUCTSTOCKLOG_ORDERCODEKEYVALUE, API_USER_WAREHOUSELIST, API_PRODUCTSTOCKLOG_OPERATEIONTYPEKEYVALUE, API_PRODUCT_GETPRODUCTKEYVALUES, API_STOCKRELOCATION_RESERVOIRKEYVALUES, API_STOCKRELOCATION_GETSTORAGELOCATIONKEYVALUES} from 'api'
import moment from 'moment'
const {RangePicker} = DatePicker

@connect(
  state => ({...state.overYearInventory, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
    this.state = {
      isopen: false
    }
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    console.log(values, 999)
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size, userLoation} = this.props
    let reqObj = {...searchParams}

    handleGetList({searchParams: {...reqObj, page: 1, size}, userLoation})
  }

  // 重置
  reset = () => {
    const {update, userLoation, handleGetList, searchParams, size} = this.props
    update({searchParams: {warehouseCode: {value: userLoation.value, children: userLoation.children}}})
    let reqObj = {...searchParams}
    handleGetList({searchParams: {...reqObj, page: 1, size}, userLoation})
  }

  render() {
    const {
      searchParams: {
        orderCode,
        taskType,
        productCode,
        warehouseCode,
        productName,
        fromReservoirAreaCode,
        fromStoreLocationCode,
        toReservoirAreaCode,
        toStoreLocationCode,
        initDate
      },
      userLoation
    } = this.props
    let newDate = new Date()
    let dateFormat = 'YYYY-MM-DD'
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={6}>
            <div className='commonInput'>
              <label>订单查询：</label>
              <div className='box'>
                <Input value={orderCode} onChange={(e) => {
                  this.onInputChange({orderCode: e.target.value})
                }} />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_PRODUCTSTOCKLOG_OPERATEIONTYPEKEYVALUE}
              label='操作类型'
              selectItem={{data: taskType, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({taskType: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxMultipleSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品编码'
              searchParamsKey='productCode'
              searchParams={{warehouseCode: warehouseCode ? warehouseCode.value : userLoation.value}}
              selectItem={{data: productCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品名称'
              searchParamsKey='productName'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: productName, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value, n) => {
                this.onInputChange({productName: value})
              }}
              onSelect={(value) => {
                this.onInputChange({productName: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_STOCKRELOCATION_RESERVOIRKEYVALUES}
              label='从库区'
              selectItem={{data: fromReservoirAreaCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({fromReservoirAreaCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_STOCKRELOCATION_GETSTORAGELOCATIONKEYVALUES}
              label='从储位'
              searchParams={{reservoirAreaCode: fromReservoirAreaCode && fromReservoirAreaCode.value}}
              errMessage='请先选择从库区'
              selectItem={{data: fromStoreLocationCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({fromStoreLocationCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_STOCKRELOCATION_RESERVOIRKEYVALUES}
              label='至库区'
              selectItem={{data: toReservoirAreaCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({toReservoirAreaCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_STOCKRELOCATION_GETSTORAGELOCATIONKEYVALUES}
              label='至储位'
              searchParams={{reservoirAreaCode: toReservoirAreaCode && toReservoirAreaCode.value}}
              errMessage='请先选择至库区'
              selectItem={{data: toStoreLocationCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({toStoreLocationCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_USER_WAREHOUSELIST}
              label='仓库'
              selectItem={{data: warehouseCode ? warehouseCode : {value: userLoation.value, children: userLoation.children}, key: 'warehouseCode', valueKey: ['warehouseCode', 'warehouseName']}}
              onChangeSelect={(value) => {
                this.onInputChange({warehouseCode: value})
              }}
            />
          </Col>
          <Col span={10}>
            <div className='commonInput'>
              <label>历史时间：</label>
              <div className='box' style={{marginRight: 0}}>
                <RangePicker
                  value={initDate}
                  format={dateFormat}
                  defaultValue={[moment(`${newDate.getFullYear() - 1}-01-01`, dateFormat),
                    moment(`${newDate.getFullYear() - 1}-12-31`, dateFormat)]}
                  disabledDate={(current) => current && moment(current).year() > newDate.getFullYear() - 1 || moment(current).year() < newDate.getFullYear() - 1}
                  onChange={(moment, string) => {
                    this.onInputChange({initDate: moment})
                  }}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



